import { defineStore } from 'pinia';
import { ref } from 'vue';

/**
 *  Store
 *  MVU 
 */
export const useStatDataStore = defineStore('status-bar-stat-data', () => {
  // ============ State ============

  /**  */
  const data = ref<Record<string, any> | null>(null);

  /**  */
  const loading = ref(true);

  /**  */
  const error = ref<string | null>(null);

  /**  */
  const lastRefreshTime = ref<Date | null>(null);

  // ============ Actions ============

  /**
   * /
   */
  const refresh = () => {
    try {
      loading.value = true;

      // 
      const variables = getVariables({
        type: 'message',
        message_id: getCurrentMessageId(),
      });

      //  stat_data
      const statData = _.get(variables, 'stat_data', {});

      if (!statData || Object.keys(statData).length === 0) {
        throw new Error('');
      }

      data.value = statData;
      error.value = null;
      lastRefreshTime.value = new Date();
      console.log('[StatDataStore] ');
    } catch (e) {
      console.error('[StatDataStore] :', e);
      error.value = e instanceof Error ? e.message : '';
    } finally {
      loading.value = false;
    }
  };

  return {
    data,
    loading,
    error,
    lastRefreshTime,
    refresh,
  };
});
